  display: block; }

.input-label,
.input,
.button {
  background-color: transparent;
  padding: 0; }
  .input-label:hover,
  .input:hover,
  .button:hover {
    cursor: pointer; }

.input:focus,
.button:focus,
.link:focus {
  outline: none;
  box-shadow: 0 0 0 2px white, 0 0 0 4px rgba(44, 54, 242, 0.4), 0 0 0 2px rgba(255, 0, 0, 0), 0 0 0 2px white; }

.button:hover,
