    height: 25px; } }

@media screen and (max-width: 767px) {
  .logo a,
  .logo svg {
    width: 120px;
    height: 24px; } }

.list {
  list-style: none;
  margin: 0;
  padding: 0; }

.container {
  margin: 0 auto;
  max-width: 1366px;
  padding: 0 98px; }

@media screen and (max-width: 1023px) {
  .container {
    padding: 0 45px; } }

@media screen and (max-width: 767px) {
