:root {
  font-size: 62.5%; }

.body {
  color: #1f1e25;
  display: flex;
  font-family: "Roboto", Arial, "Helvetica CY", "Nimbus Sans L", sans-serif;
  font-size: 2.2rem;
  font-weight: 500;
  line-height: 1.4;
  min-width: 320px;
  position: relative;
  width: 100%; }
  .body--lock {
    overflow-y: scroll;
    position: fixed; }

.body__iefix {
  height: 100vh; }

.body__wrapper {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  z-index: 1; }

.hamburger {
  border: none;
  display: none;
  height: 11px;
  position: relative;
  width: 16px; }

@media screen and (max-width: 767px) {
  .hamburger {
    display: block; } }

.hamburger__inner {
  top: 50%;
  transform: translateY(-50%); }
  .hamburger__inner, .hamburger__inner::before, .hamburger__inner::after {
    content: "";
    background-color: black;
    height: 1px;
    left: 0;
    position: absolute;
    width: 100%;
    transition: all 0.3s ease-out; }
  .hamburger__inner::before {
    transform: translateY(5px); }
  .hamburger__inner::after {
    transform: translateY(-5px); }
  .hamburger__inner--open {
    background-color: transparent; }
    .hamburger__inner--open::before {
      transform: rotate(135deg); }
    .hamburger__inner--open::after {
      transform: rotate(-135deg); }

.nav,
.nav__item {
  margin-right: 2.5rem; }

.nav {
  padding: 0; }

.nav__links {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0; }

.nav__item a {
  line-height: 3rem; }

@media screen and (max-width: 1023px) {
  .nav {
    margin-right: 0;
    margin-left: 2rem; }
  .nav__item {
    margin-right: 2rem; }
  .nav__item a {
    line-height: 2.5rem; } }

@media screen and (max-width: 767px) {
  .nav {
    order: -1;
    margin-left: 0; }
  .nav__links {
    background-color: #f6f7ff;
    flex-direction: column;
    left: 0;
    padding: 0 15px;
    padding-left: 13px;
    position: absolute;
    top: 100%;
    width: 100%;
    z-index: 1; }
  .nav__item {
    height: 0;
    overflow-y: hidden;
    transition: height 0.3s ease-out; }
  .nav__item a {
    line-height: 2;
    margin-left: 4px; } }

.header {
  background-color: #f6f7ff; }

.header__container {
  align-items: center;
  display: flex;
  font-size: 1.6rem;
  font-weight: normal;
  line-height: 1.9rem;
  padding-bottom: 3rem;
  padding-top: 3.1rem;
  position: relative; }

.header__logo {
  flex-shrink: 0;
  margin-left: -38px;
  margin-right: 5.8rem;
  margin-top: -12px; }

.header__login {
  align-items: center;
  border: none;
  display: flex;
  flex-shrink: 0;
  margin-left: auto;
  padding: 0;
  text-align: left; }

.header__login svg {
  margin-right: 1rem; }

@media screen and (max-width: 1023px) {
  .header__container {
    font-size: 1.4rem;
    line-height: 1.6rem;
    padding-bottom: 2.2rem;
    padding-top: 2.2rem; }
  .header__logo {
    margin: 0;
    margin-right: auto; }
  .header__login svg {
    margin-right: -1px; }
  .header__login span {
    display: none; } }

@media screen and (max-width: 767px) {
  .header__container {
    padding-bottom: 1.2rem;
    padding-top: 1.2rem; }
  .header__logo {
    margin-left: 1.1rem;
    transform: translateY(-0.5rem); }
  .header__login {
    margin-right: 0; }
  .header__login svg {
    width: 14px;
    height: 16px; } }

.footer {
  background-color: #f6f7ff;
  font-size: 1.6rem;
  line-height: 2.2rem;
  margin-top: auto;
  padding: 5.5rem 0 5.4rem; }

.footer__container {
  display: flex;
  position: relative; }

.footer__logo {
  position: absolute;
  top: -7px; }

.footer__nav {
  display: flex;
  flex-shrink: 0; }

.footer__phone p,
.footer__info p {
  color: #707c87;
  font-size: 1.2rem;
  line-height: 1.7rem;
  margin: 0; }

.footer__info {
  margin-right: 80px;
  margin-top: 3.6rem; }

.footer__links {
  list-style: none;
  margin: 0;
  margin-right: 80px;
  padding: 0; }

.footer__link + .footer__link {
  margin-top: 0.9rem; }

.footer__contacts {
  align-items: flex-start;
  display: flex;
  flex-grow: 1; }

.footer__phones {
  display: flex;
  flex-grow: 1;
  flex-wrap: wrap;
  margin-top: 0.3rem;
  margin-right: auto; }

.footer__phone {
  margin-bottom: 1.5rem;
  margin-right: 80px;
  max-width: 200px;
  min-width: 140px;
  position: relative;
  width: 155px; }
  .footer__phone:nth-child(2n + 1) {
    flex-grow: 1; }

.footer__phone--cellphone {
  padding-left: 29px; }

.footer__phone-icon--cellphone {
  width: 29px; }

.footer__phone--phone {
  padding-left: 32px; }

.footer__phone-icon--phone {
  width: 32px; }

.footer__phone-icon {
  left: 0;
  position: absolute;
  top: 2px; }

.footer__social {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  margin-left: -15px;
  margin-top: 0.4rem;
  min-width: 117px;
  padding: 0; }

.footer__social-item {
  margin-left: 15px; }

.footer__social-icon {
  fill: currentColor; }

@media screen and (max-width: 1023px) {
  .footer__container {
    align-self: start; }
  .footer__nav,
  .footer__contacts,
  .footer__phones {
    flex-direction: column; }
  .footer__nav {
    margin-right: 15%; }
  .footer__logo {
    top: -6px; }
  .footer__info {
    margin: 0;
    margin-top: 2.7rem;
    order: 1; }
  .footer__links {
    margin-right: 0;
    margin-top: 4.8rem; }
  .footer__contacts {
    align-items: stretch;
    justify-content: flex-start; }
  .footer__phones {
    flex-grow: 0; }
  .footer__phone {
    margin-bottom: 2.4rem;
    margin-right: 0;
    width: 100%; }
  .footer__social {
    margin-top: 0.1rem;
    margin-left: -20px;
    max-width: 200px; }
  .footer__social-item {
    margin-left: 20px; } }

@media screen and (max-width: 767px) {
  .footer {
    padding: 4rem 0; }
  .footer__container {
    flex-direction: column; }
  .footer__logo {
    left: 10px; }
  .footer__info {
    display: none; }
  .footer__links {
    font-size: 1.4rem;
    line-height: 2rem;
    margin-bottom: 0.9rem;
    margin-top: 4.2rem; }
  .footer__link {
    margin-bottom: 1rem; }
  .footer__phone {
    margin-bottom: 1.9rem; }
  .footer__social {
    margin-top: 0; } }

.modal,
.modal__backdrop {
  height: 100%;
  width: 100%; }

.modal {
  align-items: center;
  display: flex;
  justify-content: center;
  left: 0;
  position: fixed;
  top: 0;
  z-index: 1000; }

.modal-wrapper {
  display: flex;
  max-height: 100vh;
  overflow-y: auto;
  border-radius: 8px; }

.modal__backdrop {
  background-color: #ebebeb;
  content: "";
  left: 0;
  opacity: 0.95;
  position: fixed;
  top: 0;
  z-index: -1; }

.modal__body {
  background-color: white;
  min-height: 521px;
  position: relative;
  width: 480px; }

.modal__close-btn {
  position: absolute;
  right: 32px;
  top: 32px; }
  .modal__close-btn svg {
    display: flex; }

@media screen and (max-width: 767px) {
  .modal-wrapper {
    border-radius: 0; }
  .modal__body {
    height: 100vh;
    width: 100vw; }
  .modal__close-btn {
    right: 24px;
    top: 24px; } }

.features__tabs-container {
  position: relative; }

.features__tabs {
  bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  left: 50%;
  margin-left: -8px;
  position: absolute;
  transform: translateX(-50%);
  z-index: 1; }

.features__tab {
  margin-left: 8px; }

.features__radio-clone {
