  border-radius: 8px;
  display: block;
  height: 8px;
  width: 8px; }
  .features__radio-clone--selected {
    background-color: #e0e0e0; }
  .features__radio-clone--selected.features__radio-clone--credits {
    background-color: #f6f7ff; }

.features__title {
  display: none; }

.features__panel-container {
  overflow: hidden;
  position: relative; }

.features__panel {
  min-height: 400px;
  user-select: none; }
  .features__panel--next {
    height: 100%;
    left: 50%;
    position: absolute;
    transform: translateX(-50%);
    top: 0;
    width: 100%; }
  .features__panel--sliding {
    transition: transform 0.3s ease-in; }

.map {
  max-width: 100%;
  overflow: hidden; }
